@import url("https://fonts.googleapis.com/css2?family=Playfair:ital,wght@1,700&family=Poppins:wght@300;400&family=Satisfy&display=swap");

* {
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair', serif; */

/* Navbar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0px 8px 6px -6px gray;
  --webkit-box-shadow: 0px 8px 6px -6px gray;
  border-bottom: solid gray !important;
}

.nav-link {
  font-weight: 300 !important;
}
.nav-link:hover {
  color: #e4a11b;
}

.active {
  border-bottom: 2px solid black;
  font-size: 15px !important;
  color: #e4a11b !important;
}
.navbar-brand {
  font-weight: 700;
  font-family: "roboto", "sensasarif";
  letter-spacing: 1.5px;
}
/* Navbar css */

/* Footer Start*/
.footer {
  color: white;
  padding: 25px;
  background: linear-gradient(to right, #434343, #000000) !important;
}
.footer p {
  border-bottom: 2px solid #827717;
  padding-bottom: 10px;
}

.footer a {
  font-size: 20px;
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #e4a11b;
  border-bottom: 3px solid #e4a11b;
}
/* Footer End*/

/* Page Not Found Start */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.pnf-title {
  font-size: 100px;
  font-weight: 700;
}
.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  font-size: 20px;
  color: red;
  border-bottom: 1px solid #000;
  text-decoration: none;
  margin-top: 10px;
}

/* Page Not Found End */

/* Get All Product LinK */
.product-link {
  text-decoration: none !important;
  color: #000 !important;
}
