/* CSS for the search container */
.search-container {
  max-width: 400px; /* Adjust the width as needed */
  margin: 0 auto; /* Center the container horizontally */
}

/* CSS for the search form */
.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f0f0f0;
  border-radius: 8px;
  padding: 8px;
}

/* CSS for the search input */
.search-input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* CSS for the search button */
.search-btn {
  margin-left: 8px;
  padding: 8px 16px;
  border: 1px solid #e4a11b;
  border-radius: 4px;
  background-color: #e4a11b;
  color: #fff;
  cursor: pointer;
}

/* Styling the search button when hovered */
.search-btn:hover {
  background-color: #a97308;
}

/* Styling the search button when clicked */
.search-btn:active {
  background-color: #82662e;
}
